body,html{font-family:'Roboto', sans-serif;height:100%;margin:0;padding:0;width:100%;font-size:10pt;font-style:normal;letter-spacing:0.05em}#app{margin:0;padding:0;height:100%}.content{height:100%;display:flex;flex-flow:column;max-width:500px;margin:0 auto}.content .main-container{position:relative;overflow:hidden;height:100%;width:100%;pointer-events:none}.content .main-container>div{white-space:pre-wrap}p{font-style:italic}h1,h2,h3,h4{font-style:italic}h1{font-size:2rem;color:red;text-align:center}h2{font-size:1.0rem;margin:0.3rem;text-align:center}h2.slideheader{position:absolute;top:15px;left:0;right:0;pointer-events:none}h3{font-size:1.0rem;margin:0.3rem;text-align:left}h4{margin:5px}button,.button{font-style:italic;margin:10px auto;display:block;background-color:#525252;color:#ffffff;padding:5px 10px;font-weight:bold;border-radius:5px;font-size:1rem;width:200px;border:none;text-align:center}button:disabled,.button:disabled{background-color:#ddd}button:focus,.button:focus{outline:0}a{text-decoration:none;color:#000000}p{max-width:500px}
