.slide-container{position:absolute;bottom:0;left:0;width:100%;pointer-events:auto;transition:bottom 500ms ease}.slide-container.fullscreen{height:100%}.slide-container.fullscreen.closed{bottom:-100%}.slide-container.halfscreen{height:50%}.slide-container.halfscreen.closed{bottom:-50%}.slide-container .slide-inner-container{padding-top:45px;background-color:#f0f;height:100%}.slide-container .slide-close-button{position:absolute;top:0;left:0px;text-align:center;width:100%}.slide-container .slide-close-button button{background:none;width:42px;height:42px;margin:5px;padding:4px}.slide-container .slide-close-button button img{width:100%;height:100%}.slide-container .slide-close-button.detached{top:-25px;left:10px}.slide-container .slide-close-button.detached button{background:#fff;border-radius:50%}.slide-container .drag-handle{position:absolute;top:0;left:100px;right:100px}.slide-container .drag-handle>div{margin:8px auto;background:cyan;height:5px;width:50px;border-radius:2px;opacity:0.8}
